irection: column;\n  gap: 8px;\n\n  .dashboard {\n    display: flex;\n    flex: 0 1 auto;\n    flex-wrap: wrap;\n\n    gap: 8px;\n\n    .widget {\n      display: flex;\n      flex: 1 1 0;\n      flex-direction: column;\n      align-items: center;\n      padding: 6px;\n\n      background-color: $greyLight;\n      border: 1px solid $greyBorder;\n      border-radius: 3px;\n\n      h3 {\n        white-space: nowrap;\n        font-size: 1rem;\n        font-weight: 500;\n      }\n\n      p {\n        white-space: nowrap;\n        font-size: 1rem;\n        font-weight: 400;\n      }\n    }\n  }\n\n  .charts {\n    flex: 1 1 auto;\n    display: grid;\n    grid-template-columns: 1fr 0.6fr;\n    grid-template-rows: 1fr;\n    gap: 8px;\n\n    .chartDiv {\n      // chart.js canvases need to be placed inside another element, otherwise css won't work properly\n      background-color: $greyLight;\n      border: 1px solid $greyBorder;\n      border-radius: 3px;\n    }\n  }\n}\n\nfooter {\n  flex: 0 1 auto;\n\n  text-align: center;\n  padding: 0.65rem;\n  font-size: 0.65rem;\n\n  a {\n    text-decoration: none;\n    color: $fontBlue;\n  }\n}\n\n@media screen and (max-width: 1100px) {\n  main {\n    .charts {\n      grid-template-columns: 1fr;\n      grid-template-rows: 1fr 0.6fr;\n    }\n  }\n}\n\n@media screen and (max-height: 600px) {\n  main {\n    .chartDiv {\n      min-height: 300px; // for landscape mode\n    }\n  }\n}\n"],"sourceRoot":""}]);
